
import Toast from 'vux/src/components/toast/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import tools from '../../lib/tools.js';
export default {
name:'familyAllDoctor',
components:{Toast,Tab, TabItem},
data(){
return {
errorImg:'this.src="' + require('../../assets/morenshangpin3x.png') + '"' ,
showToast:false,
toastMsg:'',
jySignType:1,
familyDoctor:[],
managerId:'',
orgId:'',
signDoctor:[],
buildType:{1:false,3:false} /*1家医 3孕产妇*/
}
},
mounted(){
var vm = this;
let id = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id'));
if(!id || id=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}// 此时可以跳转到登陆，等登陆后在跳转回来
this.managerId = id;
this.init();
this.queryDoctors();
},
methods:{
init(jySignType=1){
var vm = this;
let orgId = tools.getRequestParam('orgId');
if(!orgId){
vm.showToast = true;
vm.toastMsg = '非法操作';
return ;
}
this.orgId = orgId;
console.log('init');
this.$http.axios({method:'POST',url:'/jyDoctorInfo/getJySignDoctorInfo',data:{showCount:100,currentPage:1,signOrgId:orgId,validTime:tools.simpleDateFormat(new Date(),'yyyy-MM-dd'),jySignType:jySignType}}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.familyDoctor = data.items;
vm.jySignType = jySignType;
}else{
console.log(data.resultCode);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
},
queryDoctors(){
let vm = this;

this.$http.get('/api/contracte/queryDoctors?managerId='+this.managerId).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.signDoctor = data.data;
for(let i=0;i<data.data.length;i++){
if(data.data[i].buildType==1){
vm.buildType[1] = true;
}else if(data.data[i].buildType==3){
vm.buildType[3] = true;
}
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
signUp(id,name){
let orgName = tools.getRequestParam('orgName');
let cityCodeDesc = tools.getRequestParam('cityCodeDesc');
if(this.jySignType==1 && !this.buildType[1]){

tools.next(`/m/familySignUpCreate.html?doctorId=${id}&doctorName=${name}&orgId=${this.orgId}&orgName=${orgName}&cityCodeDesc=${cityCodeDesc}`);
return ;
}else if(this.jySignType==3 && !this.buildType[3]){

tools.next(`/m/gravidaSignCreate.html?doctorId=${id}&doctorName=${name}&orgId=${this.orgId}&orgName=${orgName}&cityCodeDesc=${cityCodeDesc}`);
}else{
this.toastMsg = '已签约医生,请到个人中心我的签约查看';
this.showToast = true;
}
},
goToDoctorIndex(id){
tools.next('/m/doctorIndex.html?md='+id);
},
toggleTab(type){
this.init(type);
}
}
}



